.orangogo-custom-selector {
  position: relative;
  font-size: 14px;
}

.orangogo-custom-selector__input {
  padding: 5px 20px 5px 10px;
  border: 1px solid #D4D4D4;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  position: relative;
  cursor: pointer;
}

.orangogo-custom-selector__input i {
  position: absolute;
  right: 10px;
  top: 0;
  bottom: 0;
  height: fit-content;
  margin: auto;
  color: #4A4A4A;
}

.orangogo-custom-selector__input--empty {
  color: #e7e7e7;
}

.orangogo-custom-selector__list {
  position: absolute;
  background-color: white;
  width: 100%;
  top: 33px;
  border: 1px solid #D4D4D4;
  border-top: 0;
  padding: 10px;
  z-index: 100;
  margin: 0;
  list-style: none;
}

.orangogo-custom-selector__list__item {
  padding: 4px 0;
  border-top: 2px solid #e7e7e7;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.orangogo-custom-selector__list__item:first-child {
  border-top-width: 0;
}

.orangogo-custom-selector__list__item i {
  margin-right: 10px;
  color: #f4812e;
}

.orangogo-checkbox {
  min-height: unset;
}

.orangogo-checkbox input + .custom-control-label {
  text-transform: none;
  font-size: 13px;
}

.orangogo-checkbox input + .custom-control-label:before {
  border: 1px solid #17BEBB;
  margin-top: -2px;
}

.orangogo-checkbox input + .custom-control-label:after {
  margin-top: -2px;
}

.orangogo-checkbox input:not(:checked) + .custom-control-label:before {
  background-color: transparent;
}

body.compensate-for-scrollbar {
  overflow: hidden;
}

.fancybox-active {
  height: auto;
}

.fancybox-is-hidden {
  left: -9999px;
  margin: 0;
  position: absolute !important;
  top: -9999px;
  visibility: hidden;
}

.fancybox-container {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,sans-serif;
  height: 100%;
  left: 0;
  position: fixed;
  -webkit-tap-highlight-color: transparent;
  top: 0;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  width: 100%;
  z-index: 99992;
}

.fancybox-container * {
  box-sizing: border-box;
}

.fancybox-bg,
.fancybox-inner,
.fancybox-outer,
.fancybox-stage {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.fancybox-outer {
  -webkit-overflow-scrolling: touch;
  overflow-y: auto;
}

.fancybox-bg {
  background: #1e1e1e;
  opacity: 0;
  transition-duration: inherit;
  transition-property: opacity;
  transition-timing-function: cubic-bezier(0.47, 0, 0.74, 0.71);
}

.fancybox-is-open .fancybox-bg {
  opacity: .87;
  transition-timing-function: cubic-bezier(0.22, 0.61, 0.36, 1);
}

.fancybox-caption,
.fancybox-infobar,
.fancybox-navigation .fancybox-button,
.fancybox-toolbar {
  direction: ltr;
  opacity: 0;
  position: absolute;
  transition: opacity .25s,visibility 0s linear .25s;
  visibility: hidden;
  z-index: 99997;
}

.fancybox-show-caption .fancybox-caption,
.fancybox-show-infobar .fancybox-infobar,
.fancybox-show-nav .fancybox-navigation .fancybox-button,
.fancybox-show-toolbar .fancybox-toolbar {
  opacity: 1;
  transition: opacity .25s,visibility 0s;
  visibility: visible;
}

.fancybox-infobar {
