.company-detail {
  background-color: #f8f8f8;
  padding: 40px 0 60px;
}

.company-detail__header {
  display: flex;
  margin-top: 64px;
}

@media (max-width: 991.98px) {
  .company-detail__header {
    align-items: center;
    margin-top: 0;
  }
}

.company-detail__header__logo {
  width: 280px;
  height: 280px;
  min-width: 280px;
  min-height: 280px;
  border-radius: 50%;
  text-align: center;
  line-height: 280px;
  background-color: white;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
  margin-right: 60px;
}

.company-detail__header__logo img {
  max-width: 80%;
}

@media (max-width: 991.98px) {
  .company-detail__header__logo {
    width: 100px;
    height: 100px;
    min-width: 100px;
    min-height: 100px;
    line-height: 100px;
    margin-right: 10px;
  }

  .company-detail__header__logo img {
    width: 55px;
  }
}

.company-detail__header__btn--contact {
  background-color: white;
  color: #f4812e;
  padding: 14px 48px;
  font-size: 16px;
}

@media (max-width: 991.98px) {
  .company-detail__header__btn--contact {
    width: 100%;
    margin-top: 30px;
    font-size: 15px;
    padding: 10px 0;
  }
}

.company-detail__header__name {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 40px;
}

@media (max-width: 991.98px) {
  .company-detail__header__name {
    font-size: 26px;
    margin-bottom: 0;
  }

  .company-detail__header__name + div {
    position: absolute;
  }
}

.company-detail__header__federation {
  padding: 0 25px;
  font-size: 16px;
  border-left: 1px solid #D4D4D4;
}

.company-detail__header__federation:first-child {
  margin-left: 15px;
  border-left: none;
}

@media (max-width: 991.98px) {
  .company-detail__header__federation {
    margin-left: 0;
    text-align: center;
    flex: 1;
    margin-top: 25px;
  }
}

.company-detail__header__practiced {
  margin-top: 40px;
}

.company-detail__header__practiced h5 {
  margin-bottom: 15px;
}

@media (max-width: 991.98px) {
  .company-detail__header__practiced h5 {
    font-size: 22px;
  }
}

.company-detail__header__practiced__sport {
  padding: 5px 15px;
  color: white;
  background-color: #17BEBB;
  margin-right: 15px;
  margin-bottom: 15px;
  border-radius: 2px;
  display: inline-block;
}

.company-detail__courses {
  margin-top: 40px;
  border-bottom: 1px solid #D4D4D4;
  margin-bottom: 35px;
}

@media (max-width: 991.98px) {
  .company-detail__courses {
    margin-top: 10px;
  }
}

.company-detail__courses__title {
  font-size: 28px;
  margin-bottom: 15px;
}

@media (max-width: 991.98px) {
  .company-detail__courses__title {
    font-size: 22px;
    margin-bottom: 0;
  }
}

.company-detail__courses__filters {
  position: relative;
  z-index: 1;
}

.company-detail__courses__filters::after {
  display: block;
  clear: both;
  content: "";
}

.company-detail__courses__filters .orangogo-custom-selector {
  width: 180px;
  text-transform: uppercase;
  float: left;
  margin-right: 20px;
  font-size: 13px;
  background-color: white;
}

.company-detail__courses__filters .orangogo-custom-selector__input {
  padding: 8px 25px 8px 15px;
  border-radius: 5px;
  border-color: #17BEBB;
}

.company-detail__courses__filters .orangogo-custom-selector__input i {
  font-size: 20px;
}

.company-detail__courses__filters .orangogo-custom-selector__list {
  border-color: #17BEBB;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  padding: 7px 20px;
}

.company-detail__courses__filters .orangogo-custom-selector__list__item {
  padding: 10px 0;
  white-space: nowrap;
  border-width: 1px;
}

.company-detail__courses__filters .orangogo-custom-selector__list__item i {
  display: none;
}

.company-detail__courses .home-carousel {
  position: relative;
  top: -30px;
}

.company-detail__additional__map {
  background-color: white;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.company-detail__additional__map__header {
  display: flex;
  align-items: start;
  padding: 10px;
}

.company-detail__additional__map__header img {
  width: 25px;
}

.company-detail__additional__map__header h6 {
  margin-bottom: 5px;
  margin-top: 8px;
  font-weight: bold;
}

.company-detail__additional__map__header p {
  margin: 0;
}

.company-detail__additional__text-collapsible {
  font-size: 16px;
}

.company-detail__additional__text-collapsible a {
  color: #a6a4a4;
  font-size: 12px;
  text-transform: uppercase;
  text-decoration: none;
}

.company-detail__reviews {
  margin-top: 50px;
}

@media (max-width: 991.98px) {
  .company-detail__reviews {
    display: none;
  }
}

.company-detail__reviews__title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 8px;
}

.company-detail__reviews__description {
  font-size: 14px;
